import common from "../../common/common.js";
export default {
	data() {
		return {
			sex:1
		}
	},
	methods: {
		//跳转
		modifyNickname(){
			uni.navigateTo({
				url:"../meNickname/meNickname"
			})
		},
		
		modifyBirthday(){
			uni.navigateTo({
				url:"../meBirthday/meBirthday"
			})
		},
		modifySex(){
			uni.navigateTo({
				url:"../sex/sex"
			})
		},
		
		//清楚缓存
		cleanStorage(){
			uni.clearStorageSync();
			uni.showToast({
				title:"清楚缓存成功",
				mask:false,
				duration:1500
			})
		},
		logout(){
			var globalUser = this.getGlobalUser("globalUser");
			var serverUrl = common.serverUrl;
			uni.request({
				url:serverUrl+'/user/logout?userId='+globalUser.id,
				method:"POST",
				success: (res) => {
					console.log(res);
					if(res.data.status == 200){
						uni.removeStorageSync("globalUser");
						uni.switchTab({
							url:"../me/me"
						})
					}
				}
			})
		},
		
		//预览和修改用户头像
		operator(){
			var _this = this;
			var globalUser = _this.getGlobalUser("globalUser");
			uni.showActionSheet({
				itemList:["查看头像","从相册中选择"],
				success(res) {
					console.log(res);
					var index = res.tapIndex;
					if(index == 0){
						//预览头像
						var faceArr = [];
						faceArr.push(globalUser.faceImage);
						uni.previewImage({
							urls:faceArr,
							current:faceArr[0]
						})
					}else if(index == 1){
						//选择上传头像
						uni.chooseImage({
							count:1,
							sizeType:["compressed"],
							sourceType:["album"],
							success(res) {
								console.log(res);
								//获得临时路径
								var tempFilePath = res.tempFilePaths[0];
								





								//图片剪裁

									uni.navigateTo({
										url: "../faceCrop/faceCrop?tempFilePath=" + tempFilePath
									})

							}
						})
						
					}
				}
			})
		}
	}
}
